.containerMessage {
    width: 300px;
    height: 75px;
    display: flex;
    background-color: cadetblue;
    padding: 10px;
    margin-left: 30px;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
}


.container {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    background-color: purple;
    border-radius: 5%;
    padding: 10px 10px 10px 15px;
}


.name {
    color: orange;

}

.message {
    color: aqua;
}

.time {
    align-self: flex-end;

}